import SmoothScroll from 'smooth-scroll'

addEventListener('DOMContentLoaded', function (){
    const scrollLink = new SmoothScroll('a[href*="#"]', {
        ignore: 'a[href="#"]',
        speed: 2400,
        easing: 'easeInOutQuint',
        updateURL: false,
        speedAsDuration: true,
    });
})